<template>
  <div>
    <shareBox v-if="showShare"  @shareAction='shareAction'></shareBox>
  </div>
</template>
<script>
import wx from 'weixin-js-sdk'
import shareBox from '@/components/common/share_box'
export default {
  components: {
    shareBox
  },
  props: {
    tag: {
      type: String
    },
    appShare: {
      type: Object
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  data () {
    return {
      shareParams: null, // 分享文案参数
      showShare: false,
      setdomain: '' // 配置中转页域名
    }
  },
  methods: {
    // 分享
    shareAction (actions) {
      if (actions === 'cancel') {
        this.showShare = false
      } else if (actions === 'msg') {
        this.$utils.plusReady(plus => {
          let msg = window.plus.messaging.createMessage(window.plus.messaging.TYPE_SMS)
          msg.to = ['10086']
          msg.bodyType = 'text'
          msg.body = `${this.shareParams.title}↓↓↓点击查看↓↓↓${this.$config.currentWebUrl}?path=${this.$route.path}&inviteCode=${this.$store.state.common.userInfo.invitation}`
          window.plus.messaging.sendMessage(msg)
        })
      } else if (actions === 'system') {
        let msg = {
          title: this.shareParams.title,
          content: this.shareParams.desc,
          thumbs: [this.shareParams.img_url],
          href: this.shareParams.shareLink
        }
        this.$utils.plusReady(plus => {
          plus.share.sendWithSystem(msg, res => {
            this.showShare = false
            this.setCallBack({type: 'app', way: 4})
          }, err => {
            this.showShare = false
            console.log(err)
          })
        })
      } else {
        let index = 0
        switch (actions) {
          case 'wechat': index = 1
            break
          case 'friend': index = 2
            break
          case 'qq': index = 3
            break
          case 'sinaweibo': index = 4
            break
          default: console.log(actions)
        }
        this.currentShare(index)
      }
    },
    // 获取随机文案
    getRandomArticle (cb) {
      let data = {
        tag: this.tag,
        url: encodeURIComponent(location.href),
        is_small_routine: 0
      }
      this.$api.get('RAMDOM_ARC', data, res => {
        this.shareParams = res.data
        let linkParams = {}
        if (this.appShare.path) {
          linkParams.path = this.appShare.path
        } else {
          linkParams = {
            download_path: '/download/index.html',
            invite: this.$store.state.common.userInfo.invitation
          }
        }
        if (this.appShare.path && res.data.gid && res.data.tag === 'car_20_25') {
          linkParams.gid = res.data.gid
        }
        if (this.commonState.login_bol) {
          linkParams.inviteCode = this.commonState.userInfo.invitation
        }
        this.$api.get('USE_DOMAIN', { type: 'temp' }, res => {
          this.$config.currentWebUrl = res.data.domain
        })
        if (this.$config.source_style >= 6) {
          this.shareParams.shareLink = `${this.$config.currentWebUrl}/?${this.$qs.stringify(linkParams)}`
        } else {
          this.shareParams.shareLink = `${location.origin}/?${this.$qs.stringify(linkParams)}`
        }
        this.$emit('getArc', this.shareParams)
        typeof cb === 'function' && cb()
      })
    },
    /** 设置分享回调函数
     * @param { Object } obj 'wechat','app'. way 1、微信好友 1、朋友圈 3、qq succes：1 成功 不填 2 为失败 不存在时未知
     */
    setCallBack ({type, way, success}) {
      this.$emit('shareSuccess', {type, way, success})
    },
    // 配置微信分享
    setWechatConfig () {
      let newUrl
      if (location.href.indexOf('#') > -1) {
        newUrl = location.href.split('#')[0]
      } else {
        newUrl = location.href
      }
      this.$api.get('WECHAT_SIGNATURE', {url: newUrl, origin_url: location.origin}, res => {
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: res.data.appid, // 必填，公众号的唯一标识
          timestamp: res.data.timestamp, // 必填，生成签名的时间戳
          nonceStr: res.data.nonceStr, // 必填，生成签名的随机串
          signature: res.data.signature, // 必填，签名
          jsApiList: res.data.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
        })
        this.$emit('getWx', wx)
        wx.ready(() => {
          // 微信好友分享
          wx.onMenuShareAppMessage({
            title: this.shareParams.title,
            link: this.shareParams.shareLink,
            desc: this.shareParams.desc,
            imgUrl: this.shareParams.img_url,
            success: (e) => {
              this.setCallBack({type: 'wechat', way: '1', success: 1})
              // let temp = e.errMsg.split(':')
              // if (temp[1] === 'ok') {
              //   this.$toast({
              //     message: '分享成功',
              //     duration: 1500
              //   })
              // } else {
              //   this.setCallBack({type: 'wechat', way: '1', success: 2})
              //   this.$toast({
              //     message: '取消分享',
              //     duration: 1500
              //   })
              // }
            }
          })
          // 微信朋友圈分享
          wx.onMenuShareTimeline({
            title: this.shareParams.title,
            link: this.shareParams.shareLink,
            desc: this.shareParams.desc,
            imgUrl: this.shareParams.img_url,
            success: (e) => {
              // let temp = e.errMsg.split(':')
              this.setCallBack({type: 'wechat', way: '2', success: 1})
              // if (temp[1] === 'ok') {
              //   this.$toast({
              //     message: '分享成功',
              //     duration: 1500
              //   })
              // } else {
              //   this.$toast({
              //     message: '取消分享',
              //     duration: 1500
              //   })
              // }
            }
          })
          // 分享给qq
          wx.onMenuShareQQ({
            title: this.shareParams.title,
            link: this.shareParams.shareLink,
            desc: this.shareParams.desc,
            imgUrl: this.shareParams.img_url,
            success: () => {
              this.setCallBack({type: 'wechat', way: '3', success: 1})
              // this.$toast({
              //   message: '分享成功',
              //   duration: 1500
              // })
            },
            cancel: () => {
              this.setCallBack({type: 'wechat', way: '3', success: 2})
              this.$toast({
                message: '已取消分享',
                duration: 1500
              })
            }
          })
        })
      })
    },
    // app 分享
    currentShare (index) {
      if (this.$config.source_style >= 6) {
        // index 1 微信好友 2 微信朋友圈 3 为qq
        let msg = {
          title: this.shareParams.title,
          content: this.shareParams.desc,
          thumbs: [this.shareParams.img_url],
          href: this.shareParams.shareLink
        }
        let id = 'weixin'
        if (index === 1) {
          id = 'weixin'
          msg.type = 'web'
          msg.extra = {
            scene: 'WXSceneSession'
          }
        } else if (index === 2) {
          id = 'weixin'
          msg.type = 'web'
          msg.extra = {
            scene: 'WXSceneTimeline'
          }
        } else if (index === 3) {
          id = 'qq'
          // msg.type = 'text'
        } else if (index === 4) {
          id = 'sinaweibo'
        }
        setTimeout(() => {
          // w.close()
        }, 5000)
        if (index === 4) {
          if (!this.commonState.shares['sinaweibo'].nativeClient) {
            this.$toast({
              message: '请先安装新浪微博',
              duration: 1500
            })
            return
          }
          this.commonState.shares['sinaweibo'].authorize(() => {
            this.commonState.shares['sinaweibo'].send(msg, (res) => {
              this.$toast({
                message: '分享成功',
                duration: 1500
              })
              this.showShare = false
            }, err => {
              // w.close()
              this.showShare = false
              if (err.code === -2) {
                this.$toast({
                  message: '已取消分享',
                  duration: 1500
                })
                this.shareSuccess({
                  type: 'app',
                  way: '3',
                  success: 2
                })
              } else {
                this.$toast({
                  message: '分享失败',
                  duration: 1500
                })
              }
            })
          })
        }
        if (index !== 4) {
          this.commonState.shares[id].send(msg, res => {
            if (index === 3) {
              // w.close()
              this.$toast({
                message: '分享成功',
                duration: 1500
              })
            }
            this.setCallBack({type: 'app', way: index})
            this.showShare = false
          }, err => {
            // w.close()
            this.showShare = false
            if (err.code === -2) {
              this.$toast({
                message: '已取消分享',
                duration: 1500
              })
              this.setCallBack({
                type: 'app',
                way: '3',
                success: 2
              })
            } else {
              this.$toast({
                message: '分享失败',
                duration: 1500
              })
            }
          })
        }
      }
    },
    // 设置app分享
    setAppShare () {
      this.showShare = true
    },
    // 初始化函数
    initShare () {
      if (this.$config.source_style >= 6 || this.$utils.isWx()) {
        this.getRandomArticle(() => {
          if (this.$utils.isWx()) {
            this.setWechatConfig()
          }
        })
      }
    }
  },
  watch: {
    'commonState.login_box' (flag) {
      if (!flag && this.commonState.login_bol) {
        this.initShare()
      }
    },
    'appShare.count': function () {
      this.setAppShare()
    }
  },
  mounted () {
    this.initShare()
  }
}
</script>



