<template>
  <div></div>
</template>
<script>
import wx from 'weixin-js-sdk'
export default {
  props: {
    tag: {
      type: String
    },
    appShare: {
      type: Object
    },
    fatherParams: {
      type: Object
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  data () {
    return {
      shareParams: null, // 分享文案参数
      setdomain: '' // 配置中转页域名
    }
  },
  methods: {
    // 获取随机文案
    getRandomArticle (cb) {
      let linkParams = {
        path: this.appShare.path ? this.appShare.path : '/' // 传递地址进来
      }
      if (this.commonState.login_bol) {
        linkParams.inviteCode = this.commonState.userInfo.invitation
        linkParams.gid = this.fatherParams.gid
      }
      this.$api.get('USE_DOMAIN', { type: 'temp' }, res => {
        this.$config.currentWebUrl = res.data.domain
        if (this.$config.source_style >= 6) {
          this.shareParams.shareLink = `${this.$config.currentWebUrl}?${this.$qs.stringify(linkParams)}`
        } else {
          this.shareParams.shareLink = `${location.origin}?${this.$qs.stringify(linkParams)}`
        }
        typeof cb === 'function' && cb()
        this.$emit('getArc', this.shareParams)
      })
    },
    /** 设置分享回调函数
     * @param { Object } obj 'wechat','app'. way 1、微信好友 1、朋友圈 3、qq succes：1 成功 不填 2 为失败 不存在时未知
     */
    setCallBack ({type, way, success}) {
      this.$emit('shareSuccess', {type, way, success})
    },
    // 配置微信分享
    setWechatConfig () {
      if (this.commonState.shareParams) {
        wx.config({
          debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
          appId: this.commonState.shareParams.appid, // 必填，公众号的唯一标识
          timestamp: this.commonState.shareParams.timestamp, // 必填，生成签名的时间戳
          nonceStr: this.commonState.shareParams.nonceStr, // 必填，生成签名的随机串
          signature: this.commonState.shareParams.signature, // 必填，签名
          jsApiList: this.commonState.shareParams.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
        })
        this.wxReady()
      } else {
        let newUrl
        if (location.href.indexOf('#') > -1) {
          newUrl = location.href.split('#')[0]
        } else {
          newUrl = location.href
        }
        this.$api.get('WECHAT_SIGNATURE', {url: newUrl, origin_url: location.origin}, res => {
          wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: res.data.appid, // 必填，公众号的唯一标识
            timestamp: res.data.timestamp, // 必填，生成签名的时间戳
            nonceStr: res.data.nonceStr, // 必填，生成签名的随机串
            signature: res.data.signature, // 必填，签名
            jsApiList: res.data.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
          })
          this.wxReady()
          this.$store.commit('setShareParams', res.data)
        })
      }
    },
    wxReady () {
      wx.ready(() => {
        // 微信好友分享
        wx.onMenuShareAppMessage({
          title: this.shareParams.title,
          link: this.shareParams.shareLink,
          desc: this.shareParams.desc,
          imgUrl: this.shareParams.img_url,
          success: (e) => {
            let temp = e.errMsg.split(':')
            if (temp[1] === 'ok') {
              this.setCallBack({type: 'wechat', way: '1', success: 1})
              this.$toast({
                message: '分享成功',
                duration: 1500
              })
            } else {
              this.setCallBack({type: 'wechat', way: '1', success: 2})
              this.$toast({
                message: '取消分享',
                duration: 1500
              })
            }
          }
        })
        // 微信朋友圈分享
        wx.onMenuShareTimeline({
          title: this.shareParams.title,
          link: this.shareParams.link,
          desc: this.shareParams.desc,
          imgUrl: this.shareParams.img_url,
          success: (e) => {
            let temp = e.errMsg.split(':')
            if (temp[1] === 'ok') {
              this.setCallBack({type: 'wechat', way: '2', success: 1})
              this.$toast({
                message: '分享成功',
                duration: 1500
              })
            } else {
              this.setCallBack({type: 'wechat', way: '2', success: 2})
              this.$toast({
                message: '取消分享',
                duration: 1500
              })
            }
          }
        })
        // 分享给qq
        wx.onMenuShareQQ({
          title: this.shareParams.title,
          link: this.shareParams.link,
          desc: this.shareParams.desc,
          imgUrl: this.shareParams.img_url,
          success: () => {
            this.setCallBack({type: 'wechat', way: '3', success: 1})
            this.$toast({
              message: '分享成功',
              duration: 1500
            })
          },
          cancel: () => {
            this.setCallBack({type: 'wechat', way: '3', success: 2})
            this.$toast({
              message: '已取消分享',
              duration: 1500
            })
          }
        })
      })
    },
    // 设置app分享
    setAppShare () {
      if (this.$config.source_style >= 6) {
        // index 0 微信好友 1 微信朋友圈 2 为qq
        let msg = {
          title: this.shareParams.title,
          content: this.shareParams.desc,
          thumbs: [this.shareParams.img_url],
          href: this.shareParams.shareLink
        }
        let id = 'weixin'
        this.$utils.appShareText(index => {
          if (index === 1) {
            id = 'weixin'
            msg.type = 'web'
            msg.extra = {
              scene: 'WXSceneSession'
            }
          } else if (index === 2) {
            id = 'weixin'
            msg.type = 'web'
            msg.extra = {
              scene: 'WXSceneTimeline'
            }
          } else if (index === 3) {
            id = 'qq'
            msg.type = 'text'
          }
          this.commonState.shares[id].send(msg, res => {
            if (index === 3) {
              this.$toast({
                message: '分享成功',
                duration: 1500
              })
            }
            this.setCallBack({type: 'app', way: index})
          }, err => {
            if (err.code === -1) {
              this.$toast({
                message: '已取消分享'
              })
              this.setCallBack({
                type: 'app',
                way: '3',
                success: 2
              })
            } else {
              this.$toast({
                message: '分享失败',
                duration: 1500
              })
            }
          })
        })
      }
    },
    // 初始化函数
    initData () {
      this.shareParams = JSON.parse(JSON.stringify(this.fatherParams))
      if (this.$config.source_style >= 6 || this.$utils.isWx()) {
        if (this.$utils.isWx()) {
          this.getRandomArticle(() => {
            this.setWechatConfig()
          })
        }
      }
    }
  },
  watch: {
    'commonState.login_box' (flag) {
      if (!flag && this.commonState.login_bol) {
        this.initData()
      }
    },
    'appShare.count': function () {
      this.setAppShare()
    }
  },
  mounted () {
    
  },
  created () {
    this.initData()
  }
}
</script>



